    const onChangeTabCount = ( tabCount ) => {
        setAttributes({ tabCount: tabCount, isChanged: true });
    }

    const onChangeTabLabel = ( tabLabel, index ) => {
        setAttributes({ tab1Label: tabLabel, isChanged: true });   
    }

    const onChangeTabIcon = ( tabIcon ) => {
        setAttributes({ tab1Icon: tabIcon, isChanged: true });
    }

    const onChangeTabContent = ( tabContent )  => {
        setAttributes({ tab1Content: tabContent, isChanged: true });
    }

